'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { isAuthenticated, logout, getUsername, getProducts, getTransactions } from '@/lib/storage'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'

export default function DashboardPage() {
  const [username, setUsername] = useState('')
  const [stats, setStats] = useState({
    totalProducts: 0,
    totalTransactions: 0,
    lowStockProducts: 0,
    pendingTransactions: 0
  })
  const router = useRouter()

  useEffect(() => {
    // Check authentication
    if (!isAuthenticated()) {
      router.push('/login')
      return
    }

    // Get user info
    setUsername(getUsername())

    // Calculate stats
    const products = getProducts()
    const transactions = getTransactions()
    
    setStats({
      totalProducts: products.length,
      totalTransactions: transactions.length,
      lowStockProducts: products.filter(p => p.stok < 10).length,
      pendingTransactions: transactions.filter(t => t.status === 'menunggu_persetujuan').length
    })
  }, [router])

  const handleLogout = () => {
    logout()
    router.push('/login')
  }

  const menuItems = [
    {
      title: 'Manajemen Produk',
      description: 'Kelola produk, harga, dan stok',
      href: '/products',
      color: 'bg-blue-500'
    },
    {
      title: 'Transaksi',
      description: 'Kelola pembelian dan penjualan',
      href: '/transactions',
      color: 'bg-green-500'
    },
    {
      title: 'Laporan Stok',
      description: 'Lihat laporan stok barang',
      href: '/reports/stok',
      color: 'bg-orange-500'
    },
    {
      title: 'Laporan Keuangan',
      description: 'Laporan harian, bulanan, tahunan',
      href: '/reports/keuangan',
      color: 'bg-purple-500'
    },
    {
      title: 'QR Code Menu',
      description: 'Generate QR code untuk menu',
      href: '/qrcode',
      color: 'bg-pink-500'
    }
  ]

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold">Sistem Kasir</h1>
              <p className="text-muted-foreground">Selamat datang, {username}</p>
            </div>
            <Button onClick={handleLogout} variant="outline">
              Keluar
            </Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        {/* Hero Image */}
        <div className="mb-8">
          <img 
            src="https://placehold.co/1200x300?text=Dashboard+Kasir+Modern+dengan+Fitur+Lengkap" 
            alt="Dashboard Kasir Modern dengan Fitur Lengkap" 
            onError={(e) => { e.currentTarget.style.display='none'; }} 
            className="w-full rounded-lg object-cover" 
          />
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Total Produk
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats.totalProducts}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Total Transaksi
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats.totalTransactions}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Stok Rendah
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-orange-600">{stats.lowStockProducts}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Menunggu Persetujuan
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-red-600">{stats.pendingTransactions}</div>
            </CardContent>
          </Card>
        </div>

        {/* Menu Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {menuItems.map((item, index) => (
            <Link key={index} href={item.href}>
              <Card className="hover:shadow-lg transition-shadow cursor-pointer h-full">
                <CardHeader>
                  <div className={`w-12 h-12 rounded-lg ${item.color} mb-4`}></div>
                  <CardTitle>{item.title}</CardTitle>
                  <CardDescription>{item.description}</CardDescription>
                </CardHeader>
                <CardContent>
                  <Button variant="outline" className="w-full">
                    Buka
                  </Button>
                </CardContent>
              </Card>
            </Link>
          ))}
        </div>
      </main>
    </div>
  )
}
